import { EventNames } from "../types";
export class OrderService {
    constructor(_statefulEventEmitterService, _apiOrderService) {
        this._statefulEventEmitterService = _statefulEventEmitterService;
        this._apiOrderService = _apiOrderService;
    }
    getOrder() {
        const order = this._statefulEventEmitterService.getLast(EventNames.ORDER);
        return Object.assign({ items: [] }, order);
    }
    getValidOrder() {
        const order = this.getOrder();
        if (!this.isValid()) {
            throw new Error('Order is not valid');
        }
        return order;
    }
    sendOrder() {
        if (!this.isValid()) {
            return;
        }
        this._apiOrderService.send(this.getValidOrder());
    }
    isValid() {
        const order = this.getOrder();
        return order.email && order.address && order.phone && order.payment && order.items.length !== 0;
    }
    setPaymentMethod(paymentMethod) {
        const order = this.getOrder();
        this._statefulEventEmitterService.emit(EventNames.ORDER, Object.assign(Object.assign({}, order), { payment: paymentMethod }));
    }
    setAddress(address) {
        const order = this.getOrder();
        this._statefulEventEmitterService.emit(EventNames.ORDER, Object.assign(Object.assign({}, order), { address }));
    }
    setEmail(email) {
        const order = this.getOrder();
        this._statefulEventEmitterService.emit(EventNames.ORDER, Object.assign(Object.assign({}, order), { email }));
    }
    setPhone(phone) {
        const order = this.getOrder();
        this._statefulEventEmitterService.emit(EventNames.ORDER, Object.assign(Object.assign({}, order), { phone }));
    }
    setProducts(products) {
        const order = this.getOrder();
        this._statefulEventEmitterService.emit(EventNames.ORDER, Object.assign(Object.assign({}, order), { items: products.map(product => product.id) }));
    }
}
