import { cloneTemplate, getProductPriceText } from "../../utils/utils";
export class BasketCardComponent {
    constructor(_basketService) {
        this._basketService = _basketService;
        this._deleteProduct = (product) => {
            this._basketService.remove(product);
        };
        this._basketCardTemplate = document.querySelector('#card-basket');
    }
    createElement(product, index) {
        const basketCardElement = cloneTemplate(this._basketCardTemplate);
        const basketItemIndexElemet = basketCardElement.querySelector('.basket__item-index');
        const basketItemTitleElement = basketCardElement.querySelector('.card__title');
        const basketItemPriceElement = basketCardElement.querySelector('.card__price');
        const basketItemDeleteBtnElement = basketCardElement.querySelector('.basket__item-delete');
        basketItemIndexElemet.textContent = `${index + 1}`;
        basketItemTitleElement.textContent = product.title;
        basketItemPriceElement.textContent = getProductPriceText(product.price);
        basketItemDeleteBtnElement.addEventListener('click', () => this._deleteProduct(product));
        return basketCardElement;
    }
}
